.purchase_standing_book_order {
  &-condition {
    display: flex;
    justify-content: space-between;

    .export {
      color: '#1890ff';
      cursor: pointer;

      >span {
        margin-left: 3px;
      }
    }
  }

  &-table {
    margin-top: 20px;
    height: calc(100vh - 180px);
    overflow: auto;
  }
}
@primary-color: #1890ff;